import React, { useState } from "react";
import Modal from "react-modal";
import "./BookingModal.css";
import logo from "../img/stroki.svg";

const BookingModal = ({ isOpen, onClose, selectedDate }) => {
  const [eventDuration, setEventDuration] = useState(1); //   
  const [selectedRoom, setSelectedRoom] = useState("504"); //    

  const handleDurationChange = (e) => {
    setEventDuration(e.target.value);
  };

  const handleRoomChange = (e) => {
    setSelectedRoom(e.target.value);
  };

  return (
    <Modal isOpen={isOpen} onRequestClose={onClose} contentLabel="">
      <div className="booking-modal">
        <h2> </h2>
        <p className="selected-date"> : {selectedDate ? selectedDate.toString() : " "}</p>
        <form className="booking-form">
          <input type="text" placeholder=" " required className="booking-input" />
          <textarea placeholder=" " required className="booking-textarea"></textarea>

          {/*    */}
          <label htmlFor="event-duration"> ():</label>
          <input
            type="number"
            id="event-duration"
            value={eventDuration}
            onChange={handleDurationChange}
            min="1"
            max="12"
            required
            className="booking-input"
          />

          {/*      */}
          <label htmlFor="room-select"> :</label>
          <select
            id="room-select"
            value={selectedRoom}
            onChange={handleRoomChange}
            className="booking-input"
          >
            <option value="504">504</option>
            <option value="505">505</option>
            <option value="510">510</option>
            <option value="511">511</option>
          </select>

          <button type="submit" className="booking-submit"></button>
          <button onClick={onClose} className="booking-close"></button>
        </form>
        <img src={logo} alt="" className="auth-logo"/>
      </div>
    </Modal>
  );
};

export default BookingModal;