import React from "react";
import Modal from "react-modal";
import "./AuthModal.css";
import logo from "../img/stroki.svg";

const AuthModal = ({ isOpen, onClose }) => {
  return (
    <Modal isOpen={isOpen} onRequestClose={onClose} contentLabel="">
      <div className="auth-modal">
        <h2>  </h2>
        <form className="auth-form">
          <input type="text" placeholder="" required className="auth-input" />
          <input type="password" placeholder="" required className="auth-input" />
          <button type="submit" className="auth-submit"></button>
          <button onClick={onClose} className="auth-close"></button>
        </form>
        <img src={logo} alt="" className="auth-logo"/>
      </div>
    </Modal>
  );
};

export default AuthModal;